@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-400.woff2") format("woff2"), url("../fonts/ptmono-400.woff") format("woff"); }

html,
body {
  height: 100%; }

* {
  box-sizing: border-box; }

body {
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #666666;
  min-width: 320px; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative; }

.container {
  max-width: 1416px;
  padding: 0 15px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 25px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; } }

button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  cursor: pointer; }

.page__header {
  background-color: #1b2d37;
  background-image: url("../img/promo-bg.png"), url("../img/promo-dekor.svg");
  background-repeat: no-repeat;
  background-position: center top, calc(50% + 397px) calc(50% + 80px);
  background-size: cover, auto;
  min-width: 320px; }
  @media (max-width: 1023px) {
    .page__header {
      background-size: cover, auto 383px;
      background-position: center top, calc(50% + 312px) 82px; } }
  @media (max-width: 767px) {
    .page__header {
      min-height: auto;
      background-size: cover, auto 363px;
      background-position: center top, calc(50% + 235px) 70px; } }

.header {
  padding: 33px 0 33px 0; }
  @media (max-width: 1023px) {
    .header {
      padding: 20px 0 0 0; } }
  @media (max-width: 767px) {
    .header {
      padding: 17px 0 0 0; } }

.header__container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 14px; }
  @media (max-width: 1023px) {
    .header__container {
      padding: 0 25px; } }
  @media (max-width: 767px) {
    .header__container {
      padding: 0 15px; } }

.header__body {
  display: flex;
  align-items: center; }

.header__logo {
  margin: 0 auto 0 0;
  flex-shrink: 0;
  align-self: flex-start; }

.header__logo img {
  display: block; }
  @media (max-width: 767px) {
    .header__logo img {
      width: 38px;
      height: 38px; } }

.header__contacts {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-end;
  width: 100%; }

.header__email-link,
.header__phone-link {
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
  margin: 5px 0 5px 88px;
  position: relative; }
  .header__email-link:hover,
  .header__phone-link:hover {
    color: rgba(0, 178, 255, 0.8); }
  .header__email-link:active,
  .header__phone-link:active {
    color: rgba(0, 178, 255, 0.3); }
  @media (max-width: 1023px) {
    .header__email-link,
    .header__phone-link {
      margin: 0 0 0 58px; } }
  @media (max-width: 767px) {
    .header__email-link,
    .header__phone-link {
      margin: 0 0 0 48px; } }

.header__email-link {
  padding: 0 0 0 40px; }
  @media (max-width: 1023px) {
    .header__email-link {
      padding: 0; } }

.header__phone-link {
  padding: 0 0 0 34px; }
  @media (max-width: 1023px) {
    .header__phone-link {
      padding: 0; } }

@media (max-width: 1023px) {
  .header__email-link span,
  .header__phone-link span {
    display: none; } }

.header__email-link svg {
  margin: 2px 0 0 -40px;
  position: absolute; }
  @media (max-width: 1023px) {
    .header__email-link svg {
      width: 25px;
      height: 19px;
      position: static;
      margin: 2px 0 0 0; } }

.header__phone-link svg {
  margin: 0 0 0 -35px;
  position: absolute; }
  @media (max-width: 1023px) {
    .header__phone-link svg {
      position: static;
      width: 24px;
      height: 24px;
      margin: 0 0 0 -24px; } }

.header__button-wrapper {
  margin: 5px 0 0 51px;
  align-self: flex-start; }
  @media (max-width: 1023px) {
    .header__button-wrapper {
      display: none; } }

.header__button {
  padding: 0;
  width: 180px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .header__button:hover {
    border: 1px solid rgba(0, 178, 255, 0.8);
    color: rgba(0, 178, 255, 0.8); }
  .header__button:active {
    border: 1px solid rgba(0, 178, 255, 0.3);
    color: rgba(0, 178, 255, 0.3); }

.promo {
  overflow: hidden; }

.promo__container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 14px; }

.promo__body {
  padding: 197px 0 277px 0;
  position: relative; }
  @media (max-width: 1023px) {
    .promo__body {
      padding: 115px 0 94px 0; } }
  @media (max-width: 767px) {
    .promo__body {
      padding: 232px 0 70px 0; } }

.promo__content {
  position: relative;
  align-self: flex-start;
