      margin: 0 0 26px; } }

.promo__content p {
  font-size: 22px;
  line-height: 34px;
  color: #ffffff;
  position: relative;
  margin: 0 0 41px 0;
  padding: 0 0 0 93px;
  max-width: 56%; }
  .promo__content p::before {
    content: "";
