    background-color: #ffffff;
    top: 15px;
    left: -1px; }
  @media (max-width: 1023px) {
    .promo__content p {
      font-size: 18px;
      line-height: 28px;
      padding: 0 0 0 48px;
      margin: 0 0 54px 0;
