      margin: 0 0 40px; } }

.promo__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 407px;
  height: 55px;
  background-color: #00b2ff;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 700;
  margin: 0 0 0 95px;
  text-decoration: none; }
  .promo__button:hover {
    background-color: rgba(0, 178, 255, 0.8); }
  .promo__button:active {
    background-color: rgba(0, 178, 255, 0.3); }
  @media (max-width: 1023px) {
    .promo__button {
      width: 379px;
      margin: 0 0 70px 50px; } }
  @media (max-width: 767px) {
    .promo__button {
      width: 287px;
      margin: 0 auto; } }

.promo__image-wrapper {
  position: absolute;
  width: 705px;
  left: calc(50% + 78px);
  top: 140px;
  z-index: 0; }
  @media (max-width: 1023px) {
    .promo__image-wrapper {
      width: 465px;
      height: 340px;
      top: 41px;
      left: calc(50% + 124px);
      bottom: auto; } }
  @media (max-width: 767px) {
    .promo__image-wrapper {
      width: 301px;
